body.path-result .region--content .block-system-main-block,
body.path-result .region--content-above > * {
  grid-column: 1 / 15; }

div.esm-status-badge {
  display: grid;
  grid-template-columns: 1fr 300px;
  max-width: 900px;
  margin-bottom: 0.25em;
  color: var(--esm-c-white);
  border-radius: 5px;
  overflow: hidden;
  font-size: 0.85rem; }
  div.esm-status-badge .esm-status-badge--label {
    display: inline-block;
    padding: 0rem 1rem;
    background: var(--esm-c-black); }
  div.esm-status-badge ul {
    display: inline-flex;
    margin: 0 !important;
    padding: 0;
    list-style-type: none; }
    div.esm-status-badge ul li {
      display: inline-block;
      margin: 0;
      padding: 0 1rem;
      flex-grow: 1;
      text-align: center; }
      div.esm-status-badge ul li.sb-item--error {
        background-color: var(--esm-c-error-light);
        color: var(--esm-c-black); }
      div.esm-status-badge ul li.sb-item--warning {
        background-color: var(--esm-c-warning-light);
        color: var(--esm-c-black); }
      div.esm-status-badge ul li.sb-item--info {
        background-color: var(--esm-c-info-light);
        color: var(--esm-info-text-colo); }
      div.esm-status-badge ul li.sb-item--success {
        background-color: var(--esm-c-success-light);
        color: var(--esm-success-text-colo); }

article.result .esm-status-badge {
  margin-bottom: 0.5rem; }
